import postRobot from '../../../post-robot-wrapper';
import './google-pay.html';
import '../styles/defaults.css';
import {getComponentIdFromQueryString, getControllerIdFromQueryString} from '../querystring';
import {config} from '../config';
import {sanitizeOptionsForGoogleApplePay} from '../options';
import {addHandleOptions, addTriggerEvent, mountComponent} from '../payment-events';
import {
  emitComponentCancelled,
  emitComponentReady,
  getGoogleDefaultShippingOptions,
  getGoogleTransactionInfo,
  onPayButtonClick,
  sendClickEvent,
  sendShippingAddressChangeEvent,
  sendShippingOptionChangeEvent,
} from '../payment-api-events';
import {generateInstanceData} from '../create-initial-data';
import {generateComponentData} from '../payment-component-data';
import {processClickEvent} from '../payment-api';

const COMPONENT_TYPE = 'googlepay';

let componentData = generateComponentData(COMPONENT_TYPE, getComponentIdFromQueryString(COMPONENT_TYPE),getControllerIdFromQueryString());

const controllerListener = postRobot.listener({ window: componentData.controller.window, domain: config.domain });
const controllerEmitter = postRobot.client({ window: componentData.controller.window, domain: config.domain });
let paymentOptions = null;

const allowedCardNetworks = ['AMEX', 'DISCOVER', 'INTERAC', 'JCB', 'MASTERCARD', 'VISA'];
const allowedCardAuthMethods = ['PAN_ONLY']; // Not supporting 'CRYPTOGRAM_3DS' at the moment

function getGoogleShippingAddressParameters() {
  return  {
    phoneNumberRequired: true
  };
}

const instanceData = generateInstanceData(
  controllerEmitter,
  componentData,
  getPaymentOptions,
  getElement,
  setOptions,
);

mountComponent(controllerEmitter, componentData, handleOptions, emitComponentReady, undefined, instanceData);
addHandleOptions(controllerListener,  handleOptions);
addTriggerEvent(controllerListener, paymentApiTriggerEvent, instanceData);

const MERCHANT_ID = 'BCR2DN4T7CR25OJG';
const MERCHANT_NAME = 'Digital River';
const PUBLIC_KEY_NON_PROD = 'BLYc//KM/lJq6nN1EOVx+dnrdSvZSWb+LDpw4hdQ1JEwqG8M4YKN7QFHV/co1RMexRu09nxa4YMAk0RNLqtUwtg=';
const PUBLIC_KEY_PROD = 'BJzm/YGu+Cj1kGqyspm028M6+dYNSKjzwSfX2ppfwTRXebSSa56aKhUD/Q6e4liE6Darec5/E+zO6BwLyW2wNQg=';


function determinePublicKey(isTestEnv) {
  if (isTestEnv) {
    return PUBLIC_KEY_NON_PROD;
  } else {
    return PUBLIC_KEY_PROD;
  }
}

/**
 * Runs appropriate handler for event type
 * @param {string} type - event type
 */
export function paymentApiTriggerEvent(type) {
  switch (type) {
    case 'show' : {
      getElement().getElementsByTagName('button')[0].click();
      return;
    }
  }
}

/**
 * returns element with id of buyButton
 * @returns {HTMLElement}
 */
export function getElement() {
  return document.getElementById('container');
}

/**
 * Returns payment options
 * @returns {object}
 */
export function getPaymentOptions() {
  return paymentOptions;
}

/**
 * Applies options to google pay element
 * @param {object} data
 */
export function handleOptions(data) {
  const isStyleEqual = paymentOptions === null ? false : isObjectShallowEqual(data.options.style, paymentOptions.style);

  setOptions(data.options);
  if (typeof componentData.instanceOptions === 'undefined') {
    addInstanceOptions(data.instanceOptions);
  }

  if (!isStyleEqual) {
    onGooglePayLoaded();
  }

}

function isObjectShallowEqual(o1, o2) {
  return JSON.stringify(o1) === JSON.stringify(o2);
}

/**
 * Stores the instance options in the component state
 * @param instanceOptions
 */
export function addInstanceOptions(instanceOptions) {
  componentData.instanceOptions = instanceOptions;
}

export function getInstanceData() {
  return componentData.instanceOptions;
}

/*
Sanitizes options and puts amount in correct format and then assigns them to paymentOptions
 */
function setOptions(unsafeOptions) {
  const options = sanitizeOptionsForGoogleApplePay(unsafeOptions);
  options.total.amount = options.total.amount.toString();
  paymentOptions = options;
}

function getGoogleIsReadyToPayRequest() {
  return Object.assign(
    {},
    baseRequest,
    {
      allowedPaymentMethods: [baseCardPaymentMethod]
    }
  );
}

/**
 * Initialize Google PaymentsClient after Google-hosted JavaScript has loaded
 *
 * Display a Google Pay payment button after confirmation of the viewer's
 * ability to pay.
 */
export function onGooglePayLoaded() {
  const paymentsClient = getGooglePaymentsClient();
  return paymentsClient.isReadyToPay(getGoogleIsReadyToPayRequest())
    .then(function(response) {
      if (response.result) {
        addGooglePayButton();
      }
    })
    .catch(function(err) {
      // show error in developer console for debugging
      console.error(err);
    });
}

/**
 * Add a Google Pay purchase button alongside an existing checkout button
 *
 * @see {@link https://developers.google.com/pay/api/web/reference/request-objects#ButtonOptions|Button options}
 * @see {@link https://developers.google.com/pay/api/web/guides/brand-guidelines|Google Pay brand guidelines}
 */
function addGooglePayButton() {

  if (getElement().childNodes.length > 0) {
    getElement().innerHTML = '';
  }

  const paymentsClient = getGooglePaymentsClient();
  const color = paymentOptions.style.buttonColor === 'dark' ? 'black' : 'white';
  const type = determineButtonType(paymentOptions.style.buttonType);
  const buttonOptions = {onClick: onGooglePaymentButtonClicked, buttonSizeMode: 'fill', buttonType: type, buttonColor: color,};

  if (paymentOptions.style.buttonLanguage) {
    buttonOptions.buttonLocale = paymentOptions.style.buttonLanguage;
  }

  const button = paymentsClient.createButton(buttonOptions);
  document.getElementById('container').appendChild(button);
}

function determineButtonType(clientType) {
  if (clientType === 'plain') {
    return 'plain';
  } else if (clientType === 'subscribe') {
    return 'subscribe';
  } else {
    return 'buy';
  }
}

const baseRequest = {
  apiVersion: 2,
  apiVersionMinor: 0
};

function getTokenSpecification(isTestKey) {
  return {
    'type': 'DIRECT',
    'parameters': {
      'protocolVersion': 'ECv2',
      'publicKey':  determinePublicKey(isTestKey)
    }
  };
}


const baseCardPaymentMethod = {
  type: 'CARD',
  parameters: {
    allowedAuthMethods: allowedCardAuthMethods,
    allowedCardNetworks: allowedCardNetworks,
    billingAddressRequired: true,
    billingAddressParameters: {
      format: 'FULL',
      phoneNumberRequired: true
    },
    //assuranceDetailsRequired: true,
    //allowPrepaidCards: true
  },
};

function isRequestShippingMode() {
  return getPaymentOptions().requestShipping === true;
}

function getCardPaymentMethod(isTestApiKey) {
  return Object.assign(
    {},
    baseCardPaymentMethod,
    {
      tokenizationSpecification: getTokenSpecification(isTestApiKey)
    }
  );
}

function getGooglePaymentDataRequest() {
  const paymentDataRequest = Object.assign({}, baseRequest);
  paymentDataRequest.allowedPaymentMethods = [getCardPaymentMethod(getInstanceData().isTestApiKey)];
  paymentDataRequest.transactionInfo = getGoogleTransactionInfo(getPaymentOptions());
  paymentDataRequest.merchantInfo =  {
    merchantName: MERCHANT_NAME,
    merchantId: MERCHANT_ID
  };

  paymentDataRequest.emailRequired = true;
  if (isRequestShippingMode()) {
    paymentDataRequest.shippingAddressRequired = true;
    paymentDataRequest.shippingOptionRequired = true;
    paymentDataRequest.callbackIntents = ['SHIPPING_ADDRESS', 'SHIPPING_OPTION', 'PAYMENT_AUTHORIZATION'];
    paymentDataRequest.shippingAddressParameters = getGoogleShippingAddressParameters();
  } else {
    paymentDataRequest.shippingAddressRequired = false;
    paymentDataRequest.shippingOptionRequired = false;
    paymentDataRequest.callbackIntents = ['PAYMENT_AUTHORIZATION'];
  }


  return paymentDataRequest;
}


function removeGrayPane() {

  // Select the node that will be observed for mutations
  const targetNode = document.body;

  // Options for the observer (which mutations to observe)
  const config = { attributes: false, childList: true, subtree: true };

  // Callback function to execute when mutations are observed
  const callback = function(mutationsList, observer) {
    // Use traditional 'for loops' for IE 11
    for(const mutation of mutationsList) {
      if (mutation.type === 'childList') {
        const grayPanes = document.getElementsByTagName('gpay-graypane');
        if (grayPanes.length > 0) {
          grayPanes[0].style.backgroundColor = 'transparent';
          observer.disconnect();
        }
      }
    }
  };

  // Create an observer instance linked to the callback function
  const observer = new MutationObserver(callback);

  // Start observing the target node for configured mutations
  observer.observe(targetNode, config);

}

/**
 * Show Google Pay payment sheet when Google Pay payment button is clicked
 *
 */
export async function onGooglePaymentButtonClicked() {

  await processClickEvent(instanceData, sendClickEvent);

  removeGrayPane();

  const paymentDataRequest = getGooglePaymentDataRequest();
  paymentDataRequest.transactionInfo = getGoogleTransactionInfo(getPaymentOptions());

  const paymentsClient = getGooglePaymentsClient();

  return paymentsClient.loadPaymentData(paymentDataRequest).catch((err) => {
    if ((err.message && err.message.includes('User closed the Payment Request UI')) || (err.statusCode && err.statusCode === 'CANCELED')) {
      emitComponentCancelled(componentData);
    }
  });
  
}

function generateClientConfig() {
  const environment = getInstanceData().isTestApiKey ?  'TEST' : 'PRODUCTION';
  if (isRequestShippingMode()) {
    return {
      environment: environment,
      paymentDataCallbacks: {
        onPaymentDataChanged: onPaymentDataChanged,
        onPaymentAuthorized: onPaymentAuthorized
      }
    };
  } else {
    return {
      environment: environment,
      paymentDataCallbacks: {
        onPaymentAuthorized: onPaymentAuthorized
      }
    };
  }
}

export function onPaymentAuthorized(paymentData) {
  return onPayButtonClick(instanceData, paymentData);
}

function getGooglePaymentsClient() {
  if ( typeof instanceData.paymentsClient === 'undefined' ) {
    // eslint-disable-next-line no-undef
    instanceData.paymentsClient = new google.payments.api.PaymentsClient(generateClientConfig());
  }
  return instanceData.paymentsClient;
}

export function onPaymentDataChanged(intermediatePaymentData) {
  return new Promise(function(resolve) {

    const shippingAddress = intermediatePaymentData.shippingAddress;
    let paymentDataRequestUpdate = {};
    if (intermediatePaymentData.callbackTrigger === 'SHIPPING_ADDRESS' || intermediatePaymentData.callbackTrigger === 'INITIALIZE') {
      const selectedShippingOptionId = getGoogleDefaultShippingOptions(getPaymentOptions()).defaultSelectedOptionId;
      sendShippingAddressChangeEvent(shippingAddress, selectedShippingOptionId, resolve, instanceData);
      return;
    } else if (intermediatePaymentData.callbackTrigger === 'SHIPPING_OPTION') {
      const selectedShippingOptionId = intermediatePaymentData.shippingOptionData.id;
      sendShippingOptionChangeEvent(selectedShippingOptionId, resolve, instanceData);
      return;
    }

    resolve(paymentDataRequestUpdate);

  });
}

